export default {
	data() {
		return {






			people:{},
			userinfo: {},
			severList: [{
					name: '我的发布',
					icon: 'bank.png'
				},
				{
					name: '我的抢单',
					icon: 'mingxi.png'
				},
				{
					name: '关于',
					icon: 'point.png'
				},
				{
					name: '退出',
					icon: 'quan.png'
				},

				
				
			],
		}

	},
	onLoad() {
		//加载
		this.init();
		this.getPeople();
	},
	methods: {
		getPeople() {
			uni.request({
				url: this.$serverUrl + '/common/getMyInfo',
				
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log("请求失败", ret)
					} else {
						this.people = ret.data.data ;
						console.log(this.people);
					}
				}
			})
		},
		init() {
			//用户信息
			this.userinfo = {
				face: '../../../static/HM-PersonalCenter/face.jpeg',
			}
		},
		//用户点击订单类型
		toOrderType(index) {
			uni.showToast({
				title: this.orderTypeLise[index].name
			});
		},
		//用户点击列表项
		toPage(index) {
			if(index==0){
				console.log(index);
				uni.navigateTo({
					url: 'mypublish',
				});
			}
			
			else if(index==1){
				uni.navigateTo({
					url: 'mygrab',
				});
			}
			else if(index==2){
				
					console.log(index);
					uni.navigateTo({
						url: 'about',
					});
				
			}
			else if(index==3){
				uni.request({
					url: this.$serverUrl + '/logout',
					
					success: (ret) => {
						if (ret.data.message!="成功注销！") {
							console.log("请求失败", ret)
						} else {
							
							uni.reLaunch({
								url:'../../login/login'
							})
						}
					}
				})
			}
		}	
	}
}
